:root {
  --black: #4b4a4a;
  --green: radial-gradient(circle 675px at center, #ffffff 0%, #ccdef0 100%);
  --white: #fff;
  --white2: #fff;
  --black2: #4b4a4a;
  --blue: #5abb33;
}
.dark {
  --green: radial-gradient(circle 675px at center, #aecc76 0%, #47f04f 100%);
  --black: #fff;
  --white: #113828;
  --black2: #124e36;
  --blue: #466ef1;
  height: 100vh;
}
section {
  position: relative;
  width: 100%;
  min-height: 100%;
  padding: 100px;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
  background: var(--green);
  color: var(--black);
}

.nav-wrapper {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  padding: 20px 100px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  z-index: 10000;
}

.nav {
  position: relative;
  display: flex;
  justify-content: center;
  align-items: center;
}
.nav__item {
  list-style: none;
  display: flex;
  margin-left: 30px;
}

.nav__link {
  display: inline-block;
  color: var(--black);
  font-weight: 500;
  text-decoration: none;
  font-size: 18px;
  line-height: 1;
}
.nav__link:hover {
  color: var(--blue);
  transition: 0.2s ease-in-out;
}
.themeSwitch {
  position: relative;
  width: 40px;
  height: 40px;
  background: var(--black);
  border-radius: 50%;
  cursor: pointer;
  display: flex;
  justify-content: center;
  align-items: center;
}
.themeSwitch ion-icon {
  font-size: 1.6em;
  display: flex;
  justify-content: center;
  align-items: center;
}
.themeSwitch:hover {
  background: var(--blue);
}
.themeSwitch.active {
  background: var(--white2);
}
.themeSwitch.active:hover {
  border: 3px solid var(--blue);
  opacity: 0.7;
  transition: 0.2s all ease-in-out;
}
.themeSwitch .sunny,
.themeSwitch.active .moon {
  display: none;
}
.themeSwitch .moon,
.themeSwitch.active .sunny {
  display: flex;
  color: var(--white);
}
.logo {
  color: var(--black);
  font-size: 28px;
  font-weight: 900;
  line-height: 1;
}
.logo span {
  color: var(--blue);
  transition: 0.2s ease-in-out;
}
.header-content {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
}

.header-desk {
  width: 500px;
  padding-top: 90px;
}
.header__title {
  font-size: 48px;
  font-weight: 800;
  color: var(--black);
  line-height: 1.3;
  margin: 0;
  margin-bottom: 35px;
}
.header__text {
  font-size: 20px;
  line-height: 1.7;
  font-weight: 500;
  margin-bottom: 35px;
}
.header__btn {
  height: 65px;
  display: inline-block;
  padding: 18px 35px 21px 35px;
  border-radius: 33px;
  background-color: #fadb60;
  background-image: linear-gradient(to top, #ffc719 0%, #fadb60 100%);
  text-decoration: none;
  color: var(--black);
  font-size: 24px;
  font-weight: 700;
  line-height: 1.2;
  text-transform: uppercase;
}
.header__btn:hover {
  background-color: #e2c242;
  background-image: linear-gradient(to top, #f0b608 0%, #bba344 100%);
  color: var(--white2);
}
.header-photo {
  position: relative;
  width: 670px;
  display: flex;
  justify-content: center;
  align-items: center;
}
.sci {
  position: absolute;
  bottom: 40px;
  left: 100px;
  display: flex;
  justify-content: center;
  align-items: center;
}
.sci li {
  list-style: none;
}
.sci li a {
  display: inline-block;
  margin-right: 15px;
  width: 50px;
  height: 50px;
  background: var(--black2);
  color: var(--white2);
  border-radius: 50%;
  display: flex;
  justify-content: center;
  align-items: center;
  text-decoration: none;
}
.sci li a ion-icon {
  font-size: 1.6em;
}
.sci li a:hover {
  background: var(--blue);
}

@media (max-width: 1170px) {
  .nav-wrapper {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
    padding: 40px 40px 0px;
    margin-bottom: 70px;
  }
  section {
    padding: 100px 40px;
  }
  .header-content {
    width: 100%;
  }
  .nav__link {
    text-decoration: none;
    font-size: 18px;
    line-height: 1;
  }

  .header-desk {
    width: 440px;
    padding-top: 70px;
    padding-bottom: 100px;
  }
  .header__title {
    font-size: 30px;
    line-height: 1;
    margin: 0;
    margin-bottom: 22px;
  }
  .header__text {
    font-size: 20px;
    line-height: 1.3;
    margin-bottom: 22px;
  }
  .header__btn {
    height: auto;
    display: inline-block;
    padding: 14px 22px 14px 22px;
    font-size: 22px;
    line-height: 1.2;
  }
  .header-photo {
    width: 500px;
  }
  img {
    width: 100%;
  }
}
@media (max-width: 991px) {
  .sci {
    bottom: 20px;
    left: 20px;
  }
  .nav-wrapper {
    display: flex;
    flex-direction: column;
  }
  .navigation {
    display: none;
  }
  .navigation.active {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    background: var(--white);
    flex-direction: column;
  }
  .nav__item {
    margin: 10px 0;
  }
  .nav li a {
    font-size: 1.5em;
    font-weight: 300;
  }
  .toggle {
    position: fixed;
    right: 20px;
    top: 20px;
    width: 30px;
    height: 30px;
    background: url(../img/menu.webp) center/30px no-repeat;
    filter: invert(0);
    cursor: pointer;
    z-index: 1000;
  }
  .toggle.active {
    background: url(../img/close.webp) center/25px no-repeat;
    filter: invert(0);
  }
  .dark .toggle {
    filter: invert(1);
  }
}
@media (max-width: 767px) {
  .header-content {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
  }
  .header-desk {
    width: 95%;
  }
  .header__btn {
    font-size: 16px;
  }
  .header-photo {
    width: 95%;
  }
  .nav-wrapper {
    padding: 50px 40px 30px;
  }
}
